export default function AddTerm(props) {
  return (
    <div>
      <button onClick={() => <AfterClick />}>add term</button>
    </div>
  );
}

function AfterClick() {
  return (
    <Query query={fetchTweetsQuery}>
      {({ loading, error, data }) => {
        if (error) return <div>no data loaded</div>;
        if (loading) return <div>Loading</div>;
        console.log(data);
        return (
          <div>
            <div className="dash-main-table">
              <div>clicky bro</div>
              <style jsx>
                {`
                  .dash-main-table {
                    max-width: 50%;
                    margin: 3em auto 3em auto;
                  }
                `}
              </style>
            </div>
          </div>
        );
      }}
    </Query>
  );
}

export const fetchTweetsQuery = gql`
  query fetchTerms {
    fetchTerms {
      id
      term
    }
  }
`;
